<script>
//import VueCropper from 'vue-cropperjs';
import axios from "axios"
import 'cropperjs/dist/cropper.css';
import VueCompareImage from 'vue-compare-image';


export default {
  name: 'Main',
  props: ['info'],
  mounted () {
  },
  computed: {
    getInfo () {
      return this.info_data
    },
  },
  components : {
    //VueCropper,
    VueCompareImage
  },
  data: function() {
    return {
      mixed_img: "",
      columns: [
        {
          label: "Raw <-> Ref",
          field: 'raw_ref',
          type: 'number',
        },
        {
          label: "Result <-> Ref",
          field: 'result_ref',
          type: 'number',
        },

      ],
      info_data: this.$props.info,
      images_raw: [this.$props.info.raw_map,
               this.$props.info.raw_map_gray,
               this.$props.info.raw_map_br,
               this.$props.info.raw_map_gray_br],
      images_ref: [this.$props.info.ref_map,
                   this.$props.info.ref_map_gra],
      images_result: [this.$props.info.result_map,
                   this.$props.info.result_map_gray,
                   this.$props.info.result_map_br,
                   this.$props.info.result_map_gray_br],
      /*
      rows: [
        {id:1, 
        left:3, 
        top:this.$props.info.top,
        width:this.$props.info.width,
        height:this.$props.info.height}
      ]
      */
    }
  },


  methods: {
    interlock () {
      //this.$refs.cropper2.setCropBoxData(this.$props.info)
      //this.$refs.cropper3.setCropBoxData(this.$props.info)

    },
    cropImage() {
    //  alert(this.$refs.cropper1.getCropBoxData())
      this.$refs.cropper2.setCropBoxData(this.$refs.cropper1.getCropBoxData());
      this.$refs.cropper3.setCropBoxData(this.$refs.cropper1.getCropBoxData());
      this.$props.info.raw_map_cropped = this.$refs.cropper1.getCroppedCanvas().toDataURL();
      this.$props.info.ref_map_cropped = this.$refs.cropper2.getCroppedCanvas().toDataURL();
      this.$props.info.result_map_cropped = this.$refs.cropper3.getCroppedCanvas().toDataURL();
      this.evaluate()
     // this.$props.info = this.$refs.cropper1.getCropBoxData()
      
    },
    evaluate() {
      let me = this
      axios.post("/sendCroppedImages", {
        raw_map_cropped: this.$props.info.raw_map_cropped,
        ref_map_cropped: this.$props.info.ref_map_cropped,
        result_map_cropped: this.$props.info.result_map_cropped,
   //     $props.info: this.$pr$props.info
      }).then(function (e){
    //    me.$props.info.result_map_cropped = e.data.result.result_map_cropped;
        me.$props.info.score_raw_ref =  e.data.result.score_raw_ref;
        me.$props.info.score_result_ref =  e.data.result.score_result_ref;
        
      })
    }
    
  }
}

</script>


