      </v-col>
      <v-col cols=6>
        <v-file-input
          v-on:change="setReferenceImage"
       ></v-file-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols=12>
        <v-btn
          color="primary"
          class="mr-4"
          @click="submit"
          :disabled="isProcessing"
          block
        >
          GENERATE FACE MAP
        </v-btn>
      </v-col>
      <v-col cols=12>
        <v-btn
          color="primary"
          class="mr-4"
          @click="ask"
          block
        >
          GET RESULT
        </v-btn>
