<script>
import axios from "axios"
import * as firebase from "firebase"
import {firebaseConfig} from '../../firebase.config.js'
import * as image2base64 from 'image-to-base64'
import * as jimp from 'jimp'

if (!firebase.apps.length) {
  firebase.initializeApp(firebaseConfig)
  firebase.analytics()
}

var storageRef = firebase.storage().ref();

export default {
  name: 'Main',
  props: ["info"],
  components : {
  },
  data: () => ({
    date: null,
    user_name: "tmp",
    isProcessing:false,
    raw_img_data: "",
    ref_img_data: "",
    slides: ["assets/texture_20191201_1024.png", "assets/texture_20191201_1024.png"],
  }),


  mounted () {
   
  },
  //computed: {
  //  changeRawImage () {
  //    return this.$store.state.raw_img
  //  },
  //},
  //watch: {
  //  changeRawImage: function(){
  //    let render = new FileReader();
  //    let me = this
  //    render.onload = (e) => {
  //      me.raw_img = e.target.result
  //    }  
  //    render.readAsDataURL(this.$store.state.raw_img);    
  //  }
  //},
  methods: {
    toBlob(base64) {
      var bin = atob(base64.replace(/^.*,/, ''));
      var buffer = new Uint8Array(bin.length);
      for (var i = 0; i < bin.length; i++) {
          buffer[i] = bin.charCodeAt(i);
      }
      // Blobを作成
      try{
          var blob = new Blob([buffer.buffer], {
              type: 'image/jpeg'
          });
      }catch (e){
          return false;
      }
      return blob;
    },
    urlToBlob (url) {

      return new Promise(resolve => {
        var xhr = new XMLHttpRequest();
        xhr.onload = function() {
          resolve(new Blob([xhr.response], {type: "image/jpeg"}))
        };
        xhr.open('GET', url)
        xhr.send(); 
      })
    },
    blobToBase64 (blob) {

      return new Promise(resolve => {
        var reader = new FileReader()
        reader.onload = function () {
          resolve(reader.result)
        }
        reader.readAsDataURL(blob)
      })
    },
    //getBase64FromFile (file) {
    //  return new Promise(resolve => {
    //    let render = new FileReader();
    //    render.onload = (e) => {
    //      resolve(e.target.result)
    //    }  
    //    alert(JSON.stringify(file))
    //    alert(file)
    //    render.readAsDataURL(file);    
    //  })
    //},
    submit: function() {
      let me = this;
      this.isProcessing = true
      this.date = (new Date()).toISOString()
      storageRef.child('tmp/raw_img.jpg').put(me.toBlob(me.raw_img_data)).then(function(){
        return storageRef.child('tmp/ref_img.jpg').put(me.toBlob(me.ref_img_data))
      }).then(function() {
        return axios.post("/sendRawImages?nocache=" + new Date().getTime(), {
          invoke: true,
          date: me.date
        })
      }).then(function (){
      })
      
    },
    ask () {
      this.user_name = "tmp"
      let me = this
      storageRef.child("data/" + this.user_name + "/" + this.date + '/raw_map.jpg').getDownloadURL().then(function (url) {
          image2base64(url).then(function(myBase64){
            me.$props.info.raw_map = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/ref_map.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.ref_map = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/result_map.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.result_map = "data:image/jpeg;base64," + myBase64
          //me.$store.commit("updateViewMode", 'crop')
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/raw_map_gray.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.raw_map_gray = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/ref_map_gray.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.ref_map_gray = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/result_map_gray.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.result_map_gray = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/raw_map_gray_br.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.raw_map_gray_br = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/result_map_gray_br.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.result_map_gray_br = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/raw_map_br.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.raw_map_br = "data:image/jpeg;base64," + myBase64
        })
      })
      storageRef.child("data/" + this.user_name + "/" + this.date + '/result_map_br.jpg').getDownloadURL().then(function (url) {
        image2base64(url).then(function(myBase64){
          me.$props.info.result_map_br = "data:image/jpeg;base64," + myBase64
          me.isProcessing = false
          me.$store.commit("updateViewMode", 'crop')
        })
      }).catch(function () {
        alert("Your face has not been processed yet!")
      })
    },
    
    setRawImage: function(file) { 
    //let render = new FileReader();
      //let me = this
      this.createImage(file, "raw")
      //render.onload = (e) => {
      //  me.createImage(e.target.result, "raw")
      //}
      //render.readAsDataURL(file);
    },
    setReferenceImage: function(file) {
      this.createImage(file, "ref")
      //let render = new FileReader();
      //let me = this
      //render.onload = (e) => {
      //  me.createImage(e.target.result, "ref")
      //}  
      //render.readAsDataURL(file);    
    },
    createImage (file, frontOrSide) {
        this.overlay = true
        const me = this
        const render = new FileReader()
        render.onload = (e) => {
          jimp.read(e.target.result)
            .then((img) => {
              var w = img.bitmap.width //  width of the image
              var h = img.bitmap.height // height of the image
              let deg = 0
              if (w > h) {
                deg = 90
                var tmp = w
                w = h
                h = tmp
              }
              
              if (frontOrSide === 'raw') {
                img.rotate(deg).getBase64(jimp.MIME_JPEG, function (err, buf) {
                  //me.$store.commit('updateRawImage', buf)
                  me.raw_img_data = buf
                  me.overlay = false
                  if (err != null) {
                    alert(err)
                  }
                })
              } else {
                img.rotate(deg).getBase64(jimp.MIME_JPEG, function (err, buf) {
                  //me.$store.commit('updateRefimage', buf)
                  me.ref_img_data = buf
                  me.overlay = false
                  if (err != null) {
                    alert(err)
                  }
              })
            }
          })
        }
        render.readAsDataURL(file)
    },
    tmp: function() {
      
    }
  }
};

</script>

