import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

const initialState = {
    raw_img: '',
    ref_img: '',
    raw_map: '',
    ref_map: '',
    result_map: '',
    view_mode: "main",
    score_raw_ref: 0,
    score_result_ref: 0,
    is_gray: false,
    is_br_corr: false

}

Vue.use(Vuex)

const store = new Vuex.Store({
    state: initialState,
    plugins: [createPersistedState()],
    mutations: {
        updateRawImage (state, payload) {
            state.raw_img = payload
        },
        updateRefImage (state, payload) {
            state.ref_img = payload
        },
        updateRawMap (state, payload) {
            state.raw_map = payload
        },
        updateRefMap (state, payload) {
            state.ref_map = payload
        },
        updateResultMap (state, payload) {
            state.result_map = payload
        },
        updateViewMode (state, payload) {
            state.view_mode = payload
        },
        updateIsGray (state, payload) {
            state.is_gray = payload
        },
        updateIsBrightnessCorrection(state, payload) {
            state.is_br_corr = payload
        }
    }
})
export default store