<template>
  <div class="ViewFaceModel">
    <div class='change'>
    <canvas :id="canvasName" ></canvas>
    </div>
  </div>
</template>

<script>
  import * as THREE from 'three'
  import OrbitControls from 'three-orbitcontrols'
  import {OBJLoader} from 'three-obj-mtl-loader'
  //import {MTLLoader, OBJLoader} from 'three-obj-mtl-loader'

  export default {
    name: 'ViewFaceModel',
    props: ['objName', "canvasName"],
      methods: {
        init: function () {
          this.canvas = document.querySelector('#'+this.$props.canvasName);
          this.renderer = new THREE.WebGLRenderer({canvas:this.canvas, antialias: true});
          this.renderer.setPixelRatio(1);
          const fov = 25;     // Field of View
          const aspect = 2;   // デフォルト
          const near =10;
          const far = 5000;
          this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
          var y_pos = 90;
          this.camera.position.y = y_pos;
          this.camera.position.z = 3000;
          this.camera.lookAt(new THREE.Vector3(0, y_pos, 0));
          const width = window.innerWidth*0.5;
          const height = window.innerHeight*0.5;
          this.camera.aspect =width/height;
          this.camera.updateProjectionMatrix();
          this.renderer.setSize(width,height);
          // カメラコントロール
          this.controls = new OrbitControls(this.camera, this.canvas);
          this.controls.target = new THREE.Vector3(0, y_pos, 0);
          this.controls.update();
          //①ズーム
          this.controls.userZoom = false;    //true:ズーム操作可能,false:ズーム操作不可
          this.controls.enableZoom = false;
          this.controls.userZoomSpeed = 0;   // ズーム速度
          //②回転
          this.controls.userRotate = true;    //true:回転操作可能,false:回転操作不可
          this.controls.userRotateSpeed = 1.0;   //回転速度      
          //③パン
          this.controls.userPan =false;     //true:パン操作可能,false:パン操作不可
          this.controls.enablePan = false;
          this.controls.userPanSpeed = 0;   //パン速度
          this.controls.autoRotate = false;
          this.controls.autoRotateSpeed = 10;
          this.controls.minPolarAngle = Math.PI /2 ;
          this.controls.maxPolarAngle = Math.PI /2 ;

          // シーンを作成
          this.scene = new THREE.Scene();
          this.cubeLoader = new THREE.CubeTextureLoader();
          //this.scene.background = this.cubeLoader.load(urls);
          this.scene.background = new THREE.Color( 'black' );
          this.scene.background = new THREE.Color( 'gainsboro' );
          let me = this;
          //humanの読み込み     
          let sloader = new OBJLoader();
          sloader.setPath("/assets/");
          sloader.load(me.$props.objName, function(obj){
            obj.position.set( -300, -5400, 10); // fine-tuniing is needed..           
            obj.name = "subject" 
            //obj.scale.set(0.1, 0.1, 0.1) 
            //alert(obj.scale)      
            me.scene.add(obj);
          });
          
          /*
          this.texture = new THREE.TextureLoader();///.load('marble.jpg');//, {}, function() {this.renderer.render(this.scene, this.camera);})
          this.texture.setPath("/assets/");
          this.texture.load('marble.jpg', function(texture){
            texture.wrapS = THREE.RepeatWrapping;
            texture.wrapT = THREE.RepeatWrapping;
            texture.repeat.set( 8,8 );
            me.floorMaterial = new THREE.MeshLambertMaterial( {
            map: texture
          });
          
          var meshFloor = new THREE.Mesh(
            new THREE.BoxGeometry(2000, 0.1, 2000),
            me.floorMaterial);
            meshFloor.receiveShadow = true;
            me.scene.add(meshFloor);
          });
          */

          this.light = new THREE.DirectionalLight(0xffffff, 0.2);
          this.light.position.set(1, 1, 1);
          this.light.castShadow = true;
          this.scene.add(this.light);
          var gridHelper = new THREE.GridHelper( 10, 5 );
          this.scene.add( gridHelper );  
          var ambientLight = new THREE.AmbientLight(0xffffff, 0.4);
          this.scene.add(ambientLight); 

          this.tick();
          //this.substitute();

        },
        tick: function () {
          this.renderer.render(this.scene, this.camera); 
          requestAnimationFrame(this.tick);
        },    
      },
      mounted: function() {
        this.init();    
      },
      updated: function () {
            this.tick();
      },
      data: function () {
        return {}
      },
      computed: {
        message: {
          get() {
            return this.$store.state.message;
          },
          set(value){
            this.$store.commit("updateMessage", value);
          }
        }
      },  
    }
</script>




<style>
.change {
  width:  50%;
  height: 50%;
}
</style>